.equipments {
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
}

.equipment{
  width: calc(25% - 30px);
  margin-right: 30px;
  margin-bottom: 40px;
}

.equipment__pic{
  height: 248px;
  margin-bottom: 14px;
  border-radius: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media(max-width: 1200px){
  .equipment__pic{
    height: calc(100vw / 5);
  }
}


@media(max-width: 768px){
  .equipment{
    width: calc(50% - 30px);
  }
  .equipment__pic{
    height: calc(100vw / 2.5);
  }
}

@media(max-width: 400px){
  .equipment{
    width: calc(100% - 30px);
  }
  .equipment__pic{
    height: calc(100vw / 1.2);
  }
}