.header{
  margin-bottom: 72px;
  &__logo{
    margin-right: 40px;
  }
}

.header--transparent {
  background: rgba(254, 250, 247, 0.7);
  backdrop-filter: blur(56px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  height: 87px;
  .container{
    height: 87px;
  }
  .header__left{
    padding-left: 130px;
  }
  .header__logo{
    position: absolute;
    left: 85px;
    top: 0;
  }
  &.--unfilter{
    backdrop-filter: none;
  }
}



.header__contacts{
  margin-right: 120px;
}

.header__contact{
  font-size: 16px;
  margin-bottom: 2px;
  line-height: 19px;
  b{
    font-weight: bold;
  }
}

.header__contact--small{
  font-size: 14px;
}

.enter{
  position: relative;
  &__panel{
    position: absolute;
    top: 100%;
    width: 125px;
    border-radius: 20px;
    background-color: #F6F4F3;
    padding: 20px;
    display: none;
  }
  &__btn{
    margin-bottom: 10px;
    &:last-child{
      margin-bottom: 0px;
    }
  }
}



.burger{
  width: 32px;
  margin-left: 26px;
  height: 14px;
  width: 32px;
  &:after,
  &:before{
    content: "";
    position: absolute;
    height: 2px;
    background-color: #080E0D;
    transition: .4s;
  }
  &:after{
    width: 100%;
    top: 0;
  }
  &:before{
    width: 26px;
    bottom: 0;
  }
  &:hover{
    &:before{
      width: 100%;
    }
  }
  &.active{
    &:before{
      width: 100%;
      animation: beforeIn .4s ease both;
    }
    &:after{
      animation: afterIn .4s ease both;
    }
  }
}

@keyframes beforeIn {
  0%{
    bottom: 0;
    transform: rotateZ(0deg);
  }
  50%{
    bottom: 6px;
    transform: rotateZ(0);
  }
  100%{
    bottom: 6px;
    transform: rotateZ(45deg);
  }
}

@keyframes afterIn {
  0%{
    bottom: 0;
    transform: rotateZ(0deg);
  }
  50%{
    top: 6px;
    transform: rotateZ(0);
  }
  100%{
    top: 6px;
    transform: rotateZ(-45deg);
  }
}

.menu{
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  padding: 110px 20px 40px 60px;
  width: 100%;
  max-width: 435px;
  background-color: #fff;
  z-index: 90;
  transition: .6s ease-out;
  transform: translateY(-100%);
  &.active{
    transform: translateY(0);
    opacity: 1;
  }
}

.socials-menu {
  margin-bottom: 20px;
  .socials__link{
    opacity: 1;
  }
}

.nav{
  margin-bottom: 40px;
  &__link{
    font-size: 20px;
    line-height: 140%;
    font-weight: bold;
    margin-bottom: 20px;
    &:last-child{
      margin-bottom: 0px;
    }
  }
  &__group{
    margin-top: -8px;
    margin-bottom: 20px;
  }
  &__link-inner {
    margin-bottom: 12px;
    color: #838686;
    a{
      transition: .4s;
    }
    a:hover{
      color: #080E0D;
    }
    &:last-child{
      margin-bottom: 0px;
    }
  }
}


.menu__contacts{
  padding-bottom: 12px;
  p{
    margin-bottom: 8px;
  }
}

.en-version{
  display: flex;
  align-items: center;
  padding-top: 12px;
  font-size: 16px;
  font-weight: bold;
  max-width: 289px;
  border-top: 1px solid #E7E7E7;
  .svgsprite._web{
    margin-right: 8px;
  }
  .svgsprite._arrow-link{
    margin-left: 8px;
  }
}

.--openfilter{
  &.active{
    background-color: #080E0D;
    color: #E7E7E7;
  }
}

.mobile-btns{
  display: none;
}


@media(max-width: 1100px){
  .header__contacts{
    margin-right: 30px;
    display: none;
  }
}

@media(max-width: 980px){
  .header--transparent .header__logo{
    left: 20px;
  }
}


@media(max-width: 700px){
  .mobile-btns {
    display: block;
    margin-bottom: 16px;
  }
  .nav{
    margin-bottom: 32px;
  }
  .--mobile-fade{
    display: none;
  }
}

@media(max-width: 550px){
  .header--transparent .header__logo{
    width: 50px;
  }
  .header--transparent,
  .header--transparent .container{
    height: 60px;
  }
  .menu{
    padding-left: 20px;
  }
}