.btn{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 47px;
  padding-left: 26px;
  padding-right: 26px;
  border-radius: 25px;
  letter-spacing: 0.02em;
  transition: color .4s, background-color .4s;
  white-space: nowrap;
  &--white{
    border: 1px solid #080E0D;
    color: #080E0D;
    &:hover{
      color: #fff;
      background-color: #080E0D;
    }
  }
  &--black{
    border: 1px solid #080E0D;
    background-color: #080E0D;
    color: #FBF9F7;
    &:hover{
      color: #080E0D;
      background-color: transparent;
    }
  }
}



@media(max-width: 600px){
  .grey-form{
    padding: 30px 20px;
  }
}