.expert__work{
  margin-bottom: 20px;
  p{
    color: 282828;
    font-size: 14px;
    margin-bottom: 4px;
  }
}

.expert__photo {
  img{
    border-radius: 20px;
  }
}

.expert__top{
  margin-bottom: 60px;
}

.expert__text{
  max-width: 630px;
  color: #838686;
}

.expert__information {
  margin-bottom: 40px;
}

.education__item{
  margin-bottom: 10px;
  align-items: flex-start;
}

h2.h2.education__time{
  margin-bottom: 0px;
  text-align: right;
  transform: translateY(-5px);
}

.education__name{
  color:  #838686;
}

@media(max-width: 980px){
  h2.h2.education__time{
    text-align: left;
  }
  .education{
    max-width: 500px;
  }
}

@media(max-width: 768px){
  .expert__photo{
    margin-bottom: 20px;
  }
}