.search__block{
  margin-bottom: 20px;
  .input{
    width: 100%;
    max-width: 530px;
    margin-right: 30px;
    margin-bottom: 0px;
    input{
      border-color: #DADBDB;
      color: #DADBDB;
    }
  } 
}

.search__block{
  position: relative;
}

.search__drop{
  padding: 12px 20px;
  background: rgba(250, 248, 245, 0.8);
  backdrop-filter: blur(56px);
  border-radius: 10px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 100;
  padding-top: 10px;
  display: none;
  &.active{
    display: block;
  }
}

.search__drop-item{
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.search__drop-name{
  font-size: 14px;
  line-height: 150%;
  color: #9C9F9E;
  max-width: 277px;
}

.search__drop-tags{
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.search__drop-tag{
  font-size: 14px;
  line-height: 19px;
  padding: 4px 8px;
  border-radius: 20px;
  margin-right: 8px;
  font-family: $playfair;
  text-transform: uppercase;
  &:last-child{
    margin-right: 0px;
  }
  &.--yellow{
    background-color: #FBEDCC;
  }
  &.--green{
    background-color: #CEF3D2;
  }
  &.--red{
    background-color: #F7C1CF;
  }
  &.--blue{
    background-color: #BED1F8;
  }
  &.--purple{
    background-color: #DFCCF9;
  }
}

.search__drop-right {
  display: flex;
  align-items: center;
}

.search__drop-summ{
  font-size: 14px;
  color:#9C9F9E;
  margin-right: 20px;
  white-space: nowrap;
}

.all-services{
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
}

.all-services__item {
  width: calc(25% - 30px);
  margin-right: 30px;
  margin-bottom: 80px;
}


.all-services__pic{
  height: 157px;
  border-radius: 20px;
  margin-bottom: 14px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.all-services__name{
  font-weight: bold;
}



.all-tabs-items{
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.all-tabs-item{
  height: 37px;
  display: flex;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 14px;
  border-radius: 20px;
  color: #9C9F9E;
  cursor: pointer;
  &.active{
    font-weight: 600;
    color: #080E0D;
    background-color: #F4F2F0;
  }
}

.service__accardeon{
  background-color: #F6F4F3;
  padding: 20px 40px;
  border-radius: 20px;
  margin-bottom: 12px;
}

.service__main{
  display: flex;
  align-items: center;
  margin-bottom: 0px;
  font-size: 14px;
  font-weight: 600;
}

.service__drop {
  padding-top: 20px;
  margin-top: 20px;
  border-top: 1px solid #E7E7E7;
}

.service__item{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  &:last-child{
    margin-bottom: 0px;
  }
}

.service__name{
  font-size: 14px;
  font-weight: 600;
  color: #9C9F9E;
  max-width: 380px;
  padding-right: 13px;
  .service__item.active &{
    color: #363838;
  }
}

.service__right {
  display: flex;
  align-items: center;
}

.service__price{
  font-size: 14px;
  font-weight: 600;
  margin-right: 20px;
  color: #9C9F9E;
  .service__item.active &{
    color: #363838;
  }
}

.all-form{
  padding: 40px 20px 0 20px;
  border-radius: 20px;
  background-color: #F6F4F3;
  overflow: hidden;
  .form-bottom{
    width: calc(100% + 40px);
    margin-left: -20px;

  }
}

.all-form__summ {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
}

.all-form__text{
  font-size: 14px;
  color:  #838686;
  margin-bottom: 20px;
}

.all-form__service-title {
  font-weight: bold;
  margin-bottom: 20px;
  font-size: 14px;
}

.all-form__services{
  margin-bottom: 20px;
}

.all-form__service{
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
}

.all-form__service-name{
  position: relative;
  font-size: 12px;
  transition: .4s;
  color: #838686;
  cursor: pointer;
  &:hover{
    padding-left: 20px;
    .all-form__service-delete{
      opacity: 1;
    }
  }

}

.all-form__service-delete{
  position: absolute;
  left: 0;
  top: -3px;
  opacity: 0;
  transition: .4s;
}


.all-form__service__summ{
  font-size: 12px;
  color: #838686;
}

.all-tab{
  display: none;
  &.active{
    display: block;
  }
}

.problems-filter{
  padding: 20px 0;
  background-color: #F6F4F3;
  margin-bottom: 40px;
  .container{
    display: flex;
    align-items: center;
  }
}

.problems-filter__title{
  margin-right: 40px;
}

.problems-filter__list{
  display: flex;
}

.problems-filter__item{
  margin-right: 40px;
  color: #9C9F9E;
  &.active{
    color: #363838;
  }
  &:last-child{
    margin-right: 0px;
  }
}

@media(max-width: 980px){
  .search__drop-tags{
    display: none;
  }
  .all-services__item{
    width: calc(50% - 30px);
  }

  .all-services__pic{
    height: calc(100vw / 4);
  }
  .all-services__item{
    margin-bottom: 20px;
  }
}

@media(max-width: 600px){
  .problems-filter__item{
    margin-right: 15px;
  }
  .problems-filter__title{
    margin-right: 20px;
  }
  .service__name,
  .service__price{
    font-size: 12px;
    line-height: 14px;
  }
  .service__price{
    white-space: nowrap;
  }
  .service__accardeon{
    padding-left: 20px;
    padding-right: 20px;
  }
  .search__block {
    display: block;
    .btn{
      width: 100%;
    }
  }
  .search__input{
    margin-bottom: 10px;
  }
  .search__drop-name{
    font-size: 12px;
    padding-right: 5px;
  }
  .all-form__service__summ{
    white-space: nowrap;
  }
  .title-flex{
    display: block;
    .btn{
      margin-top: 20px;
      width: 100%;
    }
  }

  .all-tabs-items{
    white-space: nowrap;
    display: block;
    overflow-x: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
    padding-left: 20px;
    .all-tabs-item{
      display: inline-flex;
    }
  }
  .all-services__pic{
    height: calc(100vw / 2);
  }
  .all-services__item{
    width: calc(100% - 30px);
  }
}