.landing-small-title{
  text-indent: 20%;
  max-width: 986px;
  margin: 0 auto;
  line-height: 150%;
  span{
    font-style: italic;
  }
}

.callback{
  border-radius: 20px;
  display: inline-flex;
  align-items: center;
  margin-bottom: 40px;
}

.callback--grey{
  padding: 40px;
  background-color: #F6F4F3;
}

.callback__text{
  max-width: 210px;
  margin-right: 10px;
  font-size: 14px;
}

.ingredients{
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  border-top: 1px solid #E7E7E7;
  padding-left: 60px;
  padding-right: 60px;
  margin-bottom: 40px;
}

.chat__item{
  margin-bottom: 20px;
  span{
    padding: 20px 38px;
    background-color: #F4F2F0;
    border-radius: 20px;
    display: inline-block;
  }
}

.content__links{
  .arrow__link{
    margin-bottom: 12px;
  }
}

.content-price{
  padding: 20px 40px;
  border-radius: 20px;
  background-color: #F6F4F3;
  margin-bottom: 80px;
}

.content-price__head{
  padding-bottom: 20px;
  border-bottom: 1px solid #E7E7E7;
  font-size: 14px;
  margin-bottom: 20px;
}


.content-price__item{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  color: #838686;
  &:last-child{
    margin-bottom: 0px;
  }
}

.content-price__name{
  font-size: 14px;
  line-height: 150%;
  max-width: 425px;
}

.content-price__summ{
  font-size: 14px;
}

.inner-experts{
  margin-bottom: 40px;
}

.inner-experts__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
}

.inner-experts__item{
  width: calc(33.33% - 30px);
  margin-right: 30px;
  margin-bottom: 40px;
  border-radius: 20px;
  overflow: hidden;
  background-color: #F6F4F3;
}

.inner-experts__pic{
  height: 193px;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.inner-experts__info{
  padding: 20px;
}

.inner-experts__name{
  font-weight: bold;
  margin-bottom: 8px; 
}

.inner-experts__experience{
  font-size: 14px;
  color: #838686;
  margin-bottom: 14px;
}

.inner-experts__link{
  font-size: 14px;
  font-weight: bold;
}

.inner-experts__item-all{
  display: flex;
  align-items: center;
  justify-content: center;
}

.inner-experts__all{
  display: inline-block;
  max-width: 230px;
}

.reports{
  padding-bottom: 80px;
  border-bottom: 1px solid #E7E7E7;
  margin-bottom: 60px;
}

.reports-slider{
  overflow: hidden;
  max-width: 665px;
  position: relative;
}

.report__text {
  font-size: 24px;
  line-height: 29px;
  font-style: italic;
  margin-bottom: 40px;
  color: #838686;
}

.report__name{
  font-size: 20px;
  line-height: 140%;
  font-weight: bold;
  margin-bottom: 4px;
}

.report__city{
  font-size: 14px;
  line-height: 150%;
  color: #838686;
}

.report__navigation {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 20px;
  z-index: 100;
}

.report__pagination {
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  align-items: center;
}

.report__prev,
.report__next{
  cursor: pointer;
}

.problems__slider{
  overflow: hidden;
}

.problem__link{
  height: 340px;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  border-radius: 20px;
  .blog-panel__tag{
    top: 20px;
    left: 20px;
  }
}

.problem__name{
  position: absolute;
  bottom: 40px;
  left: 20px;
  color: #FBF9F7;
  font-family: $playfair;
}

.problems{
  .gallery__nav{
    top: 50%;
    transform: translateY(-50%);
    padding-left: 10px;
    padding-right: 10px;
  }
}


@media(max-width: 768px){
  .content-wrap{
    max-width: 100%;
  }
  .landing-small-title{
    text-indent: 0;
  }
  .text-right{
    text-align: left;
  }
  .ingredients{
    padding-left: 0px;
    padding-right: 0px;
  }
  .chat{
    padding-top: 30px;
  }
  .content-pic{
    margin-bottom: 10px;
  }
  .recomendation{
    margin-bottom: 30px;
  }
  .landing{
    .pl-65{
      padding-left: 0px;
    }
  }
  .content-price{
    padding-left: 20px;
    padding-right: 20px;
  }
  .content-price__summ{
    width: 60px;
    font-size: 12px;
    text-align: right;
  }
  .content-price__name{
    width: calc(100% - 60px);
    font-size: 12px;
  }
  .inner-experts__item{
    width: calc(50% - 30px);
  }
}

@media(max-width: 600px){
  .ingredients{
    flex-wrap: wrap;
  }

  .ingredients__item{
    width: 33.33%;
    margin-bottom: 5px;
  }

 
}

@media(max-width: 600px){

  .ingredients__item{
    width: 50%;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 5px;
  }
  .report__text {
    font-size: 17px;
    line-height: 23px;
  }

  .report__navigation{
    position: static;
    margin-top: 20px;
    width: 100%;
    justify-content: space-between;
  }
}


@media(max-width: 500px){
  .inner-experts__item{
    width: calc(100% - 30px);
  }
}