/* Main Colors */
$color-base : #080E0D;
$color-bg : #FBF9F7;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;

// project color




/* Fonts */
$font-family : 'MullerNarrow', sans-serif;
$playfair: 'Playfair Display', serif;
$font-title: $font-family;
$font-size : 16px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 150%;
$placeholder: #BDBDBD;



/*container*/
$container-padding-left: 85px;
$container-padding-right: 85px;
$container-width: 1950px + $container-padding-left + $container-padding-right;


/*styles*/

$brs: 10px;