.input{
  font-size: 14px;
  margin-bottom: 16px;
  input{
    width: 100%;
    height: 45px;
    border-radius: 10px;
    border: 1px solid #363838;
    z-index: 10;
    padding-left: 20px;
  }
  textarea{
    width: 100%;
    height: 100px;
    border-radius: 10px;
    border: 1px solid #363838;
    z-index: 10;
    padding-left: 20px;
    padding-top: 5px;
  }

  &--grey{
    input,
    textarea{
      border-color: #DADBDB;
    }
  }
}

.input__placeholder{
  position: absolute;
  left: 16px;
  top: 10px;
  color: #B5B7B6;
  padding-left: 4px;
  padding-right: 4px;
  transition: .4s;
  input:focus + &,
  textarea:focus + &{
    transform: translateY(-23px)scale(.9);
    z-index: 20;
    background-color: #F6F4F3;
  }
}

.fast-form__item{
  margin-bottom: 28px;
  &:last-child{
    margin-bottom: 0px;
  }
}


.filter-select__main{
  background-color: #F4F2F0;
  border-radius: 20px;
  height: 41px;
  display: inline-flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 14px;
  cursor: pointer;
  &:after{
    content: "";
    position: relative;
    width: 15px;
    height: 16px;
    background: url(../img/forms/select.svg) no-repeat center / contain;
    margin-left: 6px;
  }
}

.select__list{
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 100;
  margin-top: 5px;
  display: none;
}

.filter-select__list{
  padding: 20px;
  border-radius: 20px;
  background-color:  #F4F2F0;
  box-shadow: 0 0 0 0 rgba(#000, .3);
  width: 150px;
}

.select__item{
  margin-bottom: 5px;
  cursor: pointer;
  transition: .4s;
  &:last-child{
    margin-bottom: 0px;
  }
  &:hover{
    color: #B5B7B6;
  }
  &.active{
    color: #B5B7B6;
  }
}


.select-form {
  margin-bottom: 20px;
  .checbox__name{
    font-weight: normal;
  }
  .select__main{
    width: 100%;
    height: 45px;
    padding-left: 20px;
    border-radius: 10px;
    border: 1px solid #DADBDB;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #B5B7B6;
    &.active:after{
      transform: translateY(-50%) rotateZ(0);
    } 
    &:after{
      content: "";
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%) rotateZ(180deg);
      width: 20px;
      height: 20px;
      background: url(../img/accardeon.svg) no-repeat center / contain;
      margin-left: 6px;
      transition: .4s;
    }
  }
  .select__list{
    background-color: #fff;
    width: 100%;
    border-radius: 10px;
    background-color: #F6F4F3;
    padding: 20px;
    box-shadow: 0 3px 10px 0px rgba(0,0,0,.30);
  }
}

.checkbox{
  padding-left: 24px;
  position: relative;
  display: block;
  input{
    display: none;
  }
  input:checked ~ span{
    border-color: #363838;
    &:after{
      opacity: 1;
    }
  }
  span{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
    border-radius: 5px;
    border: 1px solid #B5B7B6;
    transition: .4s;
    cursor: pointer;
    &:after{
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 9px;
      height: 7px;
      background: url(../img/check.svg) no-repeat center / contain;
      opacity: 0;
      transition: .4s;
    }
  }
}

.mainfilter__bottom{
  overflow-y: auto;
}


.form__description{
  font-size: 12px;
  line-height: 14px;
  color: #838686;
  margin-bottom: 16px;
}

.mainfilter__form-top{
  padding: 32px 40px;
}