.crumbs{
  display: flex;
  align-items: center;
  margin-bottom: 60px;
  &__arrow{
    line-height: 0%;
  }
  svg{
    margin-right: 20px;
  }
  &__link{
    font-size: 14px;
    color: #B5B7B6;
    padding-right: 14px;
    transition: color .4s;
    &:hover{
      color: $color-text;
      &:after{
        color: #B5B7B6;
      }
    }
    &:after{
      content: "/";
      position: absolute;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
    }
    &:last-child{
      padding-right: 0px;
      &::after{
        display: none;
      }
    }
  }
}

@media(max-width: 550px){
  .crumbs{
    display: block;
    margin-bottom: 30px;
  }
  .crumbs__arrow{
    display: none;
  }
  .crumbs__link{
    font-size: 12px;
  }
}