.recover{
  h1.h1{
    margin-bottom: 10px;
  }
}
.recover-form{
  padding: 40px;
  border-radius: 20px;
  background-color:  #F6F4F3;
}

.inputs{
  width: 100%;
  max-width: 558px;
  margin-right: 40px;
}


@media(max-width: 1100px){
  .inputs{
    margin-right: 20px;
  }
  .recover-form{
    padding-left: 20px;
    padding-right: 20px;
  }
}


@media(max-width: 850px){
  .recover__group {
    display: block;
  }
  .inputs{
    margin-right: 0px;
  }
}