.expert{
  &__name{
    font-weight: bold;
    margin-bottom: 12px;
    color: #080E0D;
  }
  &__description{
    color: #838686;
    margin-bottom: 12px;
    max-width: 380px;
  }
  &__info{
    margin-bottom: 20px;
  }
  &__category{
    max-width: 190px;
  }
  &__link{
    font-weight: bold;
  }
}

@media(max-width: 768px){
  .expert__img{
    margin-bottom: 20px;
  }
}

@media(max-width: 550px){
  .expert__img{
    img{
      width: 100%;
    }
  }
}