.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
  width: 100%;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}

.page{
  padding-top: 160px;
  &.page-no-padding-bottom{
    padding-bottom: 0px;
  }
}

.text-big{
  font-size: 20px;
  line-height: 140%;
  margin-bottom: 12px;
}

.text-small{
  font-size: 14px;
}

.text-grey{
  color: #838686;
}

.text-bold{
  font-weight: bold;
}

.text-right{
  text-align: right;
}

.text-center{
  text-align: center;
}

.undertitle{
  margin-bottom: 40px;
}

.text{
  margin-bottom: 40px;
  p{
    color: #838686;
    &.text-black{
      color: #080E0D;
    }
  }
  ul{
    margin-bottom: 26px;
  }
  li{
    margin-bottom: 10px;
    color: #838686;
  }
}

.text-black{
  color: #080E0D;
}

.text-italic{
  font-style: italic;
}

.content-wrap{
  max-width: 560px;
}

.content-title{
  max-width: 220px;
}

.ul-line{
  li{
    padding-left: 50px;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      width: 20px;
      height: 1px;
      background-color: #838686;
      left: 0;
      top: 12px;
    }
  }
}

.full-pic{
  margin-bottom: 80px;
  img{
    width: 100%;
  }
}

.link{
  color: #838686;
  text-decoration: underline;
  &:hover{
    text-decoration: none;
  }
  &--small{
    font-size: 12px;
  }
  &--medium{
    font-size: 14px;
  }
}


.aside{
  .h2{
    max-width: 314px;
  }
  ul{
    max-width: 220px;
  }
}

.content-pic{
  img{
    width: 100%;
    border-radius: 20px;
  }
}


h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
}



h1{
  font-size: 38px;
  line-height: 51px;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 40px;
}

h2{
  font-size: 26px;
  line-height: 120%;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 20px;
}

h2.big-title{
  font-size: 34px;
  line-height: 41px;
  font-family: $font-family;
  text-transform: unset;
  margin-bottom: 0px;
}

h3{
  font-size: 36px;
  line-height: 120%;
  font-family: $playfair;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 10px;
}

h4{
  font-size: 18px;
  line-height: 125%;
  font-family: $playfair;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 10px;
}


h5{
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 16px;
}


@media(max-width: 980px){
  .container{
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media(max-width: 768px){
  h1.h1{
    font-size: 28px;
    line-height: 40px;
  }
  h3.h3{
    font-size: 25px;
  }
  h2.h2{
    font-size: 21px;
  }
}