.text-blocks{
  max-width: 920px;
}

.text-block{
  margin-bottom: 40px;
}

.text-block__name{
  max-width: 200px;
}

.text-block__content{
  p{
    font-size: 16px;
    line-height: 150%;
    color: #838686;
  }
  a{
    font-size: 16px;
    line-height: 150%;
    color: #838686;
    &:hover{
      text-decoration: underline;
    }
  }
}


@media(max-width: 550px){
  .page{
    padding-top: 95px;
  }
}