.title-flex{
  margin-bottom: 30px;
  position: relative;
  z-index: 100;
  .h1{
    margin-bottom: 0px;
  }
}

.blog-head{
  position: relative;
  z-index: 50;
}

.blog-inner__top{
  margin-bottom: 40px;
  .blog-panel:hover{
    &:hover{
      &:before{
        display: none;
      }
      .blog-panel__tag{
        color: #080E0D;
        background-color: #FBF9F7;

      }
    }
  }
}

.blog-inner__aside{
  max-width: 253px;
  padding-top: 20px;
  border-top: 1px solid #E7E7E7;
}

.blog-inner__message{
  font-size: 14px;
  color: #838686;
  margin-bottom: 8px;
  a{
    color: #080E0D;
  }
}

.blog-inner__date{
  margin-bottom: 40px;
  font-weight: bold;
}

.recomendation{
  max-width: 260px;
}

.recomendation__title{
  font-weight: bold;
  margin-bottom: 20px;
}

.recomendation__pic{
  overflow: hidden;
  border-radius: 20px;
  margin-bottom: 12px;
}

.blog-inner__text{
  max-width: 540px;
}

.video{
  iframe{
    width: 100%;
    height: 300px;
  }
}

.share {
  display: flex;
  align-items: center;
}

.arrow__link{
  display: flex;
  align-items: center;
  margin-right: 20px;
  &:last-child{
    margin-right: 0px;
  }
  a{
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  svg{
    margin-right: 10px;
  }
}

.socials-blog{
  margin-right: 40px;
  .socials__link{
    width: 39px;
    height: 39px;
    border-radius: 10px;
    background-color: #F4F2F0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 8px;
    opacity: 1;
  }
}


@media(max-width: 980px){
  .blog-inner__top{
    .blog-panel{
      width: 100%;
    } 
  }
}


@media(max-width: 650px){
  .blog-head{
    .title-flex{
      display: block;
      .h1{
        margin-bottom: 20px;
      }
    }
  }
  .share{
    display: block;
  }
  .socials-blog{
    margin-bottom: 10px;
  }
}

@media(max-width: 500px){
  .filter{
    display: block;
    .arrow__link{
      margin-bottom: 10px;
    }
  }

  .filter__item{
    margin-bottom: 20px;
  }
}