.fast-form{
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #F6F4F3;
  padding-top: 40px;
  padding-bottom: 40px;
}

.fast-form__item{
  .input{
    margin-bottom: 0px;
  }
}

.fast-form__info{
  max-width: 250px;
}

.fast-form__undertitle{
  font-size: 12px;
}

.fast-form__submit{
  margin-right: 12px;
}

.fast-form__description{
  max-width: 300px;
  font-size: 12px;
  color: #838686;
  a{
    color: #838686;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}


@media(max-width: 980px){
  .fast-form__info{
    margin-bottom: 30px;
  }
  .fast-form__item{
    margin-bottom: 0px;
    .input{
      margin-bottom: 20px;
    }
  }
}

@media(max-width: 600px){
  .gallery__slider{
    margin-bottom: 40px;
  }
  .fast-form__item.--flex{
    display: block;
  }
  .fast-form__submit{
    margin-bottom: 20px;
  }
  .gallery__pic{
    height: calc(100vw / 1.8);
  }
  .gallery__nav{
    top: 28vw;
  }
}