.main-slider{
  margin-bottom: 80px;
  .swiper-slide{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
  }
  .swiper-pagination-bullets .swiper-pagination-bullet{
    margin: 0px;
    margin-right: 20px;
  }
  .swiper-pagination{
    display: inline-flex;
    align-items: center;
    width: auto;
    left: 85px;
    bottom: 60px;
    
  }

  .swiper-pagination-bullet{
    width: 40px;
    height: 6px;
    border-radius: 20px;
    background-color: #fff;
    opacity: .5;
    transition: .4s;
    margin-right: 20px;
    &.swiper-pagination-bullet-active{
      width: 80px;
      height: 10px;
      background-color: #FBF9F7;
      opacity: 1;
    }
    
  }
  
}

.phone{
  z-index: 80;
  position: fixed;
  width: 72px;
  height: 72px;
  right: 85px;
  bottom: 80px;
  border-radius: 50%;
  background-color: #F1F1F1;
  box-shadow: 0px 0px 20px -8px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
}

.blogs{
  position: relative;
  z-index: 10;
}

.blogs__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
  padding-bottom: 60px;
}


.blog-panel{
  height: 340px;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 20px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  display: block;
  img{
    width: 100%;
  }
  &:hover:before{
    opacity: 1;
  }
  &:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(250, 248, 245, 0.6);
    backdrop-filter: blur(56px);
    opacity: 0;
    transition: .6s;
  }
  &--33{
    width: calc(33.33% - 30px);
  }
  &--50{
    width: calc(50% - 30px);
  }
  &--66{
    width: calc(66.67% - 30px);
  }
  &--100{
    width: calc(100% - 30px);
  }
}

.blog-panel__tags{
  position: absolute;
  left: 20px;
  top: 20px;
  display: flex;
  flex-wrap: wrap;
}

.blog-panel__tag{
  height: 22px;
  padding-left: 16px;
  padding-right: 16px;
  border-radius: 20px;
  background-color: #FBF9F7;
  margin-right: 10px;
  transition: .6s;
  margin-right: 10px;
  display: inline-flex;
  align-items: center;
  margin-bottom: 10px;
  &:last-child{
    margin-right: 0px;
  }
  .blog-panel:hover &{
    background-color: #080E0D;
    color: #FBF9F7;
  }
}


.blog-panel__info{
  padding-left: 20px;
  padding-right: 20px;
  position: absolute;
  left: 0;
  bottom: 40px;
  transition: .4s;
  width: 100%;
  color: #FBF9F7;
  transform: translateY(-23px);
  .blog-panel:hover &{
    transform: translateY(0px);
  }
}

.blog-panel__date{
  margin-bottom: 10px;
  transition: .4s;
  .blog-panel:hover &{
    opacity: 0;
  }
}

.blog-panel__title{
  font-family: $playfair;
  margin-bottom: 8px;
  .blog-panel:hover &{
    color: #080E0D;
  }
}

.blog-panel__text{
  margin-bottom: 20px;
  color: #838686;
  margin-bottom: 20px;
  max-width: 736px;
  transition: .4s;
}

.blog-panel__link{
  transition: .4s;
  color: #363838;
  font-weight: bold;
}

.blog-panel__bottom{
  margin-bottom: -100%;
  transition: .6s;
  .blog-panel:hover &{
    margin-bottom: 0;
  }
}






@media(max-width: 980px){
  .main-slider .swiper-pagination{
    left: 20px;
    bottom: 18px;
  }
  .phone{
    right: 20px;
    bottom: 40px;
    width: 40px;
    height: 40px;
    .svgsprite._phone{
      width: 14px;
    }
  }
  .main-slider .swiper-slide{
    height: calc(100vw / 1.5);
  }
  .main-slider{
    margin-bottom: 40px;
  }
  .blog-panel--33,
  .blog-panel--100,
  .blog-panel--66{
    width: calc(50% - 30px);
  }
}

@media(max-width: 600px){
  .blog-panel--33,
  .blog-panel--100,
  .blog-panel--66,
  .blog-panel--50{
    width: calc(100% - 30px);
    height: calc(100vw / 1.5);
    min-height: 270px;
  }
}

@media(max-width: 500px){
  .main-slider .swiper-pagination-bullet.swiper-pagination-bullet-active{
    width: 10px;
    height: 10px;
  }
  .main-slider .swiper-pagination-bullets .swiper-pagination-bullet{
    width: 6px;
    margin-right: 10px;
  }
  
}