.popup{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 3000;
  display: none;
  background-color: rgba(#000, .7);
  overflow-y: auto;
}

.popup__bg{
  
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.popup__panel{
  position: absolute;
  width: calc(100% - 40px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #F6F4F3;
  max-width: 530px;
  border-radius: 20px;
  z-index: 10;
  padding: 40px;
  overflow: hidden;
  &.--succed{
    display: none;
  }
  &.--fast &{
    max-width: 600px;
  }
  .form-bottom{
    width: calc(100% + 80px);
    margin-left: -40px;
    margin-bottom: -40px;
    margin-top: 16px;
    .form__submit{
      font-size: 16px;
      margin-right: 20px;
      &:last-child{
        margin-right: 0px;
      }
    }
  }
}

.popup__close{
  position: absolute;
  right: 40px;
  top: 37px;
  z-index: 50;
}

.popup__name{
  margin-bottom: 32px;
}

.form__link{
  font-size: 14px;
  line-height: 150%;
  text-decoration: underline;
  color: #838686;
  margin-bottom: 16px;
  &:hover{
    text-decoration: none;
  }
}


.form-title__description{
  color: #838686;
  margin-bottom: 32px;
  &--small{
    max-width: 325px;
  }
}



.code{
  display: flex;
  justify-content: space-between;
}

.code__input{
  height: 66px;
  width: 100%;
  max-width: 47px;
  border: 1px solid #DADBDB;
  text-align: center;
  font-size: 44px;
  margin-bottom: 32px;
}

.form__alert{
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  color: #838686;
  svg{
    margin-left: 8px;
  }
}

.popup__img {
  border-radius: 20px;
  margin-bottom: 20px;
}

.popup.--service,
.popup.--problems{
  .popup__panel{
    max-width: 830px;
    top: 60px;
    transform: translateY(0) translateX(-50%);
  }
}

.popup.--eq{
  .popup__panel{
    max-width: 787px;
    top: 30px;
    transform: translateX(-50%);
  }
  .gallery{
    overflow: hidden ;
    margin-bottom: 25px;
  }
  .gallery__nav {
    display: flex;
    justify-content: space-between;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 10px;
    padding-right: 10px;
  }
  .gallery__slider {
    margin-bottom: 0px;
  }
  
}


@media(max-width: 600px){
  .popup__panel{
    transform: translateY(0) translateX(-50%);
    top: 30px;
  }
}


@media(max-width: 500px){
  .popup__panel{
    padding-left: 20px;
    padding-right: 20px;
  }
  .popup__close{
    right: 10px;
    top: 10px;
  }
}

@media(max-width: 400px){
  .form-bottom.--flex{
    display: block;
    .btn{
      margin-bottom: 10px;
      &:last-child{
        margin-bottom: 0px;
      }
    }
  }
}