.footer{
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #F4F2F0;
  margin-top: 60px;
  &__top{
    margin-bottom: 20px;
  }
}

.socials {
  display: flex;
  &__link{
    margin-right: 20px;
    opacity: .4;
    transition: opacity .4s;
    &:hover{
      opacity: 1;
    }
    &:last-child{
      margin-right: 0px;
    }
  }
}

.develop{
  display: flex;
  align-items: center;
}


@media(max-width: 980px){
  .footer{
    padding-top: 20px;
    padding-bottom: 20px;
    .socials{
      margin-bottom: 16px;
    }
  }
  .footer__item{
    margin-bottom: 5px;
  }
}
@media(max-width: 500px){
  
  .footer__item.--flex{
    display: block;
  }
  .map-site{
    margin-bottom: 10px;
    display: block;
  }
  .develop{
    margin-top: 10px;
  }
}