
.slider-content{
  margin-bottom: 40px;
}

.slider-content__slide{
  height: 500px;
  border-radius: 20px;
}

.slider-content-overflow {
  overflow: hidden;
  width: calc(100% + 80px);
  margin-bottom: 20px;
}

.slider-content__wrap{
  width: calc(100% - 80px);
}


.slider-content__info{
  display: flex;
}

.slider-content__title{
  width: 210px;
  margin-right: 20px;
}

.slider-content__text{
  width: calc(100% - 230px);
  max-width: 455px;
}

.about__footer{
  img{
    width: 100%;
    border-radius: 20px;
  }
}

.tags{
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.tag{
  height: 47px;
  background-color: #F4F2F0;
  padding-left: 26px;
  padding-right: 26px;
  border-radius: 20px;
  margin-right: 20px;
  display: inline-flex;
  align-items: center;
  margin-bottom: 20px;
  &:last-child{
    margin-right: 0px;
  }
}


@media(max-width: 650px){
  .video iframe{
    height: calc(100vw / 2);
  }
  .slider-content__slide{
    height: calc(100vw / 2);
  }
  .slider-content__info{
    display: block;
    width: 100%;
  }
  .slider-content__title{
    width: 100%;
  }
  .slider-content__text{
    width: 100%;
  }
}