.mainfilter{
  position: fixed;
  top: 84px;
  left: 0;
  width: 100%;
  height: calc(100% - 84px);
  z-index: 95;
  background-color: #FBF9F7;
  display: none;
}


.mainfilter__left{
  padding-left: 85px;
  padding-right: 85px;
  padding-top: 64px;
  height: calc(100vh - 84px);
  overflow-y: auto;
  padding-bottom: 40px;
}

.mainfilter__tab-list{
  display: flex;
  justify-content: space-between;
  padding-bottom: 12px;
  border-bottom: 1px solid #E7E7E7;
  margin-bottom: 16px;
}

.mainfilter__tab-item{
  font-family: $playfair;
  text-transform: uppercase;
  color: #9C9F9E;
  transition: color .4s;
  cursor: pointer;
  line-height: 150%;
  &.active{
    color: inherit;
  }
}


.mainfilter__checkbox{
  margin-bottom: 8px;
}

.checbox__name{
  font-size: 14px;
  font-weight: bold;
  line-height: 150%;
  color: #9C9F9E;
  input:checked ~ &{
    color: inherit;
  }
}

.checbox__link{
  font-size: 14px;
  line-height: 150%;
  color: #838686;
  text-decoration: underline;
  display: none;
  input:checked ~ &{
    display: block;
  }
  &:hover{
    text-decoration: none;
  }
}


.accardeon__name{
  font-size: 14px;
  line-height: 19px;
  font-family: $playfair;
  text-transform: uppercase;
  height: 24px;
  display: inline-flex;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  border-radius: 20px;
  transition: background-color .4s;
  &:after{
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &.--yellow:after{
    background-color: #FBEDCC;
  }
  &.--green:after{
    background-color: #CEF3D2;
  }
  &.--red:after{
    background-color: #F7C1CF;
  }
  &.--blue:after{
    background-color: #BED1F8;
  }
  &.--purple:after{
    background-color: #DFCCF9;
  }

  .accardeon__main.active &{
    &.--yellow{
      background-color: #FBEDCC;
    }
    &.--green{
      background-color: #CEF3D2;
    }
    &.--red{
      background-color: #F7C1CF;
    }
    &.--blue{
      background-color: #BED1F8;
    }
    &.--purple{
      background-color: #DFCCF9;
    }
  }
}

.accardeon__main {
  cursor: pointer;
  position: relative;
  margin-bottom: 16px;
  &:after{
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 24px;
    height: 24px;
    background: url(../img/accardeon.svg) no-repeat center / contain;
    transform: rotateZ(180deg);
    transition: .4s;
  }
  &.active:after{
    transform: rotateZ(0deg);
  }
}

.accardeon__list{
  display: none;
  padding-bottom: 16px;
}

.accardeon-group{
  border-bottom: 1px solid #E7E7E7;
  margin-bottom: 16px;
}

.accrdeon__title{
  font-size: 18px;
  line-height: 125%;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 13px;
}


.mainfilter__tab{
  display: none;
  &.active{
    display: block;
  }
}

.mainfilter__right{
  background-color: #F6F4F3;
  display: flex;
  flex-wrap: wrap;
}

.mainfilter__choice{
  padding: 64px 40px;
  align-self: flex-start;
  width: 100%;
}

.mainfilter-tag{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
  &:hover{
    .mainfilter-tag__delete{
      opacity: 1;
    }
    .mainfilter-tag__link{
      transform: translateX(20px);
    }
  }
}

.mainfilter-tag__delete{
  position: absolute;
  left: 0;
  top: 35%;
  transform: translateY(-50%);
  opacity: 0;
  transition: .4s;
  cursor: pointer;
}

.mainfilter-tag__link{
  font-size: 14px;
  text-decoration: underline;
  padding-right: 20px;
  display: block;
  transition: .4s;
  &:hover{
    text-decoration: none;
  }
}

.mainfilter__bottom{
  align-self: flex-end;
  border-top: 1px solid #E7E7E7;
  width: 100%;
}

.mainfilter-tag__name{
  display: flex;
  align-items: center;
}

.mainfilter-tag__group{
  padding: 4px 8px;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  font-family: $playfair;
  border-radius: 20px;
  &.--yellow{
    background-color: #FBEDCC;
  }
  &.--green{
    background-color: #CEF3D2;
  }
  &.--red{
    background-color: #F7C1CF;
  }
  &.--blue{
    background-color: #BED1F8;
  }
  &.--purple{
    background-color: #DFCCF9;
  }
}


.mainfilter__form{
  .input{
    margin-bottom: 16px;
  }
}


.mainfilter__form-bottom,
.form-bottom{
  padding: 20px 40px;
  font-size: 12px;
  line-height: 130%;
  color: #838686;
  background-color: #F2F0ED;
}

.mainfilter__close{
  position: absolute;
  right: 20px;
  top: 20px;
  display: none;
  z-index: 100;
}


@media(max-width: 768px){
  .mainfilter{
    overflow-y: auto;
    height: calc(100% - 60px);
    top: 60px;
  }
  .mainfilter__left{
    overflow: inherit;
    padding-left: 20px;
    padding-right: 20px;
    height: auto;
  }
  .mainfilter__close{
    display: block;
  }
}


@media(max-width: 480px){
  .mainfilter__tab-item{
    font-size: 12px;
  }
}