.gallery{
  position: relative;
}

.gallery__slider{
  margin-bottom: 80px;
}

.gallery__pic{
  height: 340px;
  border-radius: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.gallery__description{
  transition: opacity .4s;
  opacity: 0;
  .swiper-slide-active &{
    opacity: 1;
  }
}

.gallery__nav {
  position: absolute;
  left: 0;
  width: 100%;
  top: 38%;
  transform: translateY(-50%);
  z-index: 20;
  .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.next,
.prev{
  cursor: pointer;
}

