.contacts__socials{
  margin-bottom: 40px;
}

.contacts__address{
  margin-bottom: 28px;
  p{
    margin-bottom: 4px;
  }
}

.contacts__time{
  margin-bottom: 4px;
}

.contacts__phones{
  max-width: 380px;
  margin-bottom: 32px;
}

.contacts__phone{
  font-size: 32px;
  line-height: 38px;
  padding-bottom: 12px;
  border-bottom: 1px solid #E7E7E7;
  margin-bottom: 12px;
  display: block;
  b{
    font-weight: bold;
  }
}

.contacts__emails{
  p{
    margin-bottom: 4px;
  }
}

.contacts__info {
  margin-bottom: 100px;
}

.map{
  height: 510px;
  width: 100%;
  margin-bottom: -60px;
}


@media(max-width: 980px){
  .contacts__left{
    margin-bottom: 30px;
  }
  .contacts__info{
    margin-bottom: 40px;
  }
}

@media(max-width: 600px){
  .map{
    height: calc(100vw / 1.5);
  }
}

@media(max-width: 420px){
  .contacts__small-phones{
    display: block;
    a{
      display: block;
      margin-bottom: 5px;
    }
  }
}