.account{
  min-height: calc(100vh - 87px - 284px);
}

.grey-form {
  padding: 40px;
  margin-bottom: 40px;
  background-color: #F6F4F3;
  margin-bottom: 40px;
}

.grey-form__wrap{
  max-width: 820px;
}

.account__panel{
  padding: 20px 40px;
  border-radius: 20px;
  background-color: #F6F4F3;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 40px;
}


.user__confirm{
  font-weight: 600;
}

.user__name{
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 12px;
  svg{
    margin-left: 40px;
  }
}

.account__exit,
.account__detail {
  font-weight: bold;
  cursor: pointer;
}

.user__group {
  display: flex;
  &.--noflex {
    display: block;
  }
}

.user__birthday{
  color: #838686;
  margin-bottom: 8px;
  span{
    font-weight: bold;
    margin-right: 20px;
  }
}

.user__phone{
  color: #838686;
  margin-bottom: 8px;
  span{
    font-weight: bold;
  }
}

.user__confirm{
  display: flex;
  align-items: center;
  svg{
    margin-right: 12px;
  }
}

.account__tab-items{
  display: flex;
  margin-bottom: 20px;
}

.account__tab-item{
  font-weight: bold;
  font-size: 20px;
  color: #9C9F9E;
  margin-right: 60px;
  cursor: pointer;
  &:last-child{
    margin-right: 0px;
  }
  &.active{
    color: #080E0D;
  }
}

.account__tab{
  display: none;
  &.active{
    display: block;
  }
}

.account__table-head{
  display: flex;
  justify-content: space-between;
  background-color: #F2F0ED;
  border-radius: 20px;
  align-items: center;
  height: 60px;
  margin-bottom: 12px;
  font-weight: bold;
  padding-right: 40px;
  padding-left: 40px;
  color: #838686;
}

.history-item{
  &:nth-child(1){
    width: 15%;
  }
  &:nth-child(2){
    width: 15%;
  }
  &:nth-child(3){
    width: 30%;
  }
  &:nth-child(4){
    width: 25%;
  }
  &:nth-child(5){
    width: 15%;
  }
}

.healing-item{
  &:nth-child(1){
    width: 40%;
  }
  &:nth-child(2){
    width: 60%;
  }
}

.changes-item{
  padding-right: 5px;
  padding-left: 5px;
  &:nth-child(1){
    width: 12%;
  }
  &:nth-child(2){
    width: 12%;
  }
  &:nth-child(3){
    width: 12%;
  }
  &:nth-child(4){
    width: 14%;
  }
  &:nth-child(5){
    width: 25%;
  }
  &:nth-child(6){
    width: 25%;
  }
}

.admin-events-item{
  padding-right: 5px;
  padding-left: 5px;
  &:nth-child(1){
    width: 13%;
  }
  &:nth-child(2){
    width: 10%;
  }
  &:nth-child(3){
    width: 10%;
  }
  &:nth-child(4){
    width: 10%;
  }
  &:nth-child(5){
    width: 11%;
  }
  &:nth-child(6){
    width: 11%;
  }
  &:nth-child(7){
    width: 10%;
  }
  &:nth-child(8){
    width: 10%;
  }
  &:nth-child(9){
    width: 15%;
  }
}

.acount__table-accardeon{
  background-color: #F6F4F3;
  border-radius: 20px;
  padding-right: 40px;
  padding-left: 40px;
  overflow: hidden;
}

.acount__table-main{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  font-size: 14px;
  color: #838686;
  border-bottom: 1px solid transparent;
  margin-bottom: 0px;
  &.active{
    border-color: #E7E7E7;
  }
  &:after{
    top: 20px;
    right: 0px;
  }
}


.acount__table-list{
  padding: 40px 0 0; 
}

.analysis__title{
  font-size: 20px;
  font-weight: bold;
  margin-right: 20px;
}


.analysis{
  max-width: 700px;
}


.experts__worked{
  max-width: 590px;
}

.expert__worked{
  margin-bottom: 40px;
}

.experts__worked-title{
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 20px;
}

.expert__worked-pic{
  max-width: 265px;
  margin-bottom: 20px;
  img{
    width: 100%;
    border-radius: 20px;
  }
}

.expert__worked-name{
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 8px;
}

.expert__worked-work{
  font-size: 14px;
  color: #838686;
}

.acount__photos{
  padding: 40px;
  width: calc(100% + 80px);
  margin-left: -40px;
  background-color: #F2F0ED;
}

.acount__photos-wrap{
  max-width: 660px;
}

.acount__photo{
  p{
    font-weight: bold;
    margin-bottom: 20px;
  }
  img{
    width: 100%;
    max-width: 310px;
    border-radius: 20px;
  }
}

.healing__date-title{
  margin-bottom: 40px;
}

.before-healing{
  .healing__date{
    color: #080E0D;
  }
}

.before-healing__photo{
  max-width: 214px;
  height: 214px;
  border-radius: 20px;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.healing__date {
  font-weight: bold;
  color: #363838;
}

.healing__description {
  color: #838686;
  max-width: 214px;
}

.profile-edit {
  padding-top: 20px;
  margin-top: 20px;
  border-top: 1px solid #E7E7E7;
}

.profile-edit__submit{
  margin-left: 10px;   
}

.profile-edit__wrap{
  max-width: 1000px;
}

.calendar__input{
  width: 100%;
  height: 45px;
  border: 1px solid #DADBDB;
  padding-left: 20px;
  font-size: 14px;
  border-radius: 10px;
  &::placeholder{
    color: #B5B7B6;
  }
}

.account-events {
  padding: 20px 40px;
  border-radius: 20px;
  background-color: #F6F4F3;
  margin-bottom: 40px;
}

.account-events__block {
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: 1px solid #E7E7E7;
  &:last-child{
    margin-bottom: 0px;
    padding-bottom: 0px;
    border: none;
  }
}

.account-events__block-wrap{
  display: flex;
  flex-wrap: wrap;
  max-width: 900px;
}

.account-events__item{
  width: calc(35% - 40px);
  margin-right: 40px;
  &:first-child{
    width: calc(30% - 40px);
  }
}

.account-events__btns{
  width: 100%;
  padding-top: 20px;
  p{
    width: 70%;
    color: #B5B7B6;
  }
}

.account-events__btn {
  width: calc(30% - 40px);
  margin-right: 40px;
}



.account-events__name{
  margin-right: 12px;
}

.account-event{
  p{
    color: #838686;
    margin-bottom: 5px;
  }
}

.account-event-wrap{
  display: flex;
  width: 100%;
  max-width: 900px;
}

.lk-title{
  font-weight: 600;
  margin-bottom: 20px;
  font-size: 20px;
}

.account-events__download{
  display: flex;
  align-items: center;
  padding-top: 30px;
  .lk-title{
    margin-right: 20px;
    margin-bottom: 0px;
  }
}

.search {
  background-color: #F6F4F3;
  padding: 72px 0px;
  margin-bottom: 40px;
  input{
    color: #B5B7B6;
  }
}

.user__panel{
  margin-right: 100px;
}

.user__panel--border {
  padding-left: 40px;
  border-left: 1px solid #E7E7E7;
}

.user__item{
  color: #838686;
  margin-bottom: 8px;
  span{
    font-weight: bold;
  }
}

.user__notifcation{
  display: flex;
  align-items: center;
  padding-top: 25px;
  font-size: 14px;
  svg{
    margin-right: 20px;
  }
}


.account-edit__title{
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 20px;
  svg{
    margin-left: 20px;
  }
}

.account-edit__textarea{
  width: 100%;
  height: 130px;
  border: 1px solid #363838;
  margin-bottom: 12px;
  border-radius: 20px;
  padding: 12px 26px;
  font-size: 14px;
  line-height: 150%;
  font-size: 14px;
}

.add-license{
  align-self: flex-end;
  transform: translateY(-16px);
  cursor: pointer;
}

.profile-licenses__delete{
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-60%);
  cursor: pointer;
}

.education__delete {
  transform: translateY(-12px);
}

.profile-edit__block{
  margin-bottom: 40px;
}

.accardeon__main.acount__table-auto{
  height: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: flex-start;
  padding-left: 14px;
  padding-right: 14px;
  &:after{
    display: none;
  }
}

.add-photo{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.admin-photos{
  display: flex;
  justify-content: space-between;
  margin-right: -30px;
  flex-wrap: wrap;
}

.admin-photo{
  display: flex;
  margin-right: 30px;
  width: calc(25% - 30px);
  height: 226px;
  border-radius: 20px;
  margin-bottom: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.admin-photo__date{
  position: absolute;
  top: 20px;
  left: 20px;
  height: 22px;
  padding-left: 16px;
  padding-right: 16px;
  background-color: #FBF9F7;
  border-radius: 20px;
}

.admin-photo__info {
  position: absolute;
  left: 0;
  bottom: -100%;
  width: 100%;
  padding: 20px;
  backdrop-filter: blur(56px);
  background: rgba(250, 248, 245, 0.8);
  transition: .4s;
  .admin-photo:hover &{
    bottom: 0;
  }
}

.admin-photo__name{
  line-height: 125%;
}

.admin-photo__description{
  font-size: 14px;
  color: #838686;
}


.flag-date{
  display: flex;
}

.flag-date__ico{
  margin-left: 6px;
  margin-right: 9px;
  transform: translateY(-3px);
}

.acount__table-accardeon{
  margin-bottom: 12px;
  &.--yellow{
    background-color: #FBEDCC;
  }
  &.--green{
    background-color: #CEF3D2;
  }
  &.--red{
    background-color: #FBEBE1;
  }
  &.--blue{
    background-color: #BED1F8;
  }
  &.--purple{
    background-color: #DFCCF9;
  }
}


.admin-editor{
  background-color: #F6F4F3;
  width: calc(100% + 80px);
  margin-left: -40px;
}

.admin-editor__top{
  display: flex;
  justify-content: space-between;
  padding: 28px 40px 40px;
}

.admin-editor__name {
  display: flex;
  font-size: 24px;
  margin-bottom: 24px;
  svg{
    transform: translateY(5px);
    margin-left: 12px;
  }
}

.admin-editor__iser-contacts{
  display: flex;
  align-items: center;
  font-size: 20px;
  p{
    margin-right: 20px;
  }
}

.admin-editor__top-status{
  width: 100%;
  max-width: 306px;
}

.admin-editor__top-date{
  font-size: 14px;
  color: #838686;
  margin-bottom: 24px;
  text-align: right;
}

.admin-editor__edit-profile {
  padding: 20px 40px;
  background-color: #F2F0ED;
  margin-bottom: 40px;
  .profile-edit{
    margin-top: 0px;
    border: none;
    padding-top: 0px;
  }
}

.admin-editor__events{
  padding: 0 40px 40px;
}

.text-radios{
  display: flex;
  margin-bottom: 28px;
}

.text-radio{
  cursor: pointer;
  margin-right: 20px;
  input{
    display: none;
    &:checked + span{
      border-color: #000;

    }
  }
  span{
    height: 64px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    min-width: 193px;
    border: 1px solid #DADBDB;
    border-radius: 20px;
  }
}


.admin-editor__patient{
  margin-bottom: 20px;
  .search__drop-name {
    display: flex;
    max-width: 480px;
    transition: .4s;
    color: #080E0D;
    &:hover{
      padding-left: 20px;
      .search__drop-delete{
        opacity: 1;
      }
    }
  }
  .search__drop-summ{
    color: #080E0D;
  }
}

.search__drop-delete {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-65%);
  opacity: 0;
  transition: .4s;
  cursor: pointer;
}

.admin-editor__summ{
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
}


.admin__editor-textarea{
  height: 140px;
  border-radius: 10px;
  padding: 12px 20px;
  border: 1px solid #DADBDB;
  width: 100%;
  max-width: 305px;
}

.user__notconfirm{
  display: flex;
  align-items: center;
  a{
    margin-left: 20px;
    font-size: 14px;
    text-decoration: underline;
    color: #838686;
    &:hover{
      text-decoration: none;
    }
  }
  svg{
    margin-right: 12px;
  }
}

.admin-edit__user-btns{
  padding-top: 20px;
  margin-top: 20px;
  border-top: 1px solid #E7E7E7;
}

.admin-edit__user-btn{
  margin-right: 20px;
  &:last-child{
    margin-right: 0px;
  }
}

.acount__photo-img{
  position: relative;
}

.acount__photo-edit{
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 10;
}